.mat-select {
  width: 100%;
}
.mat-select-trigger,
.mat-option {
  font-size: 16px;
}
.mat-select-placeholder {
  //line-height: normal;
  padding: 0;
}
.mat-select-underline {
  bottom: 1px;
  background-color: rgba(0, 0, 0, 0.12);
}
.app-dark .mat-select-panel {
  background-color: $white-color;
}

.mat-select-placeholder {
  position: relative;
  padding: 0 2px;
  transform-origin: left top;
  flex-grow: 1;
}
.mat-select-value,
.mat-select-trigger {
  color: #3c4858 !important;
}
.mat-form-field.mat-focused.mat-primary .mat-select-arrow {
  color: rgba(0, 0, 0, 0.54);
}
//
.mat-select-panel {
  .mat-pseudo-checkbox {
    border: none !important;
  }
}
.mat-pseudo-checkbox {
  margin-top: 2px;
  float: right;
}
.mat-select-panel {
  background: white;
}
.mat-option.mat-selected:not(.mat-option-multiple) {
  background-color: orange !important;
  color: black !important;
}
.mat-option {
  color: rgba(0, 0, 0, 0.87) !important;
  transition: all 150ms linear;

  &:hover {
    @include box-shadow($shadow-mat-select-option);
    background: #ff9a01 !important;
    color: $white-color !important;
  }
  &:focus {
    color: rgba(0, 0, 0, 0.87) !important;
    background: transparent !important;
    .mat-option-pseudo-checkbox {
      &:after {
        color: $white-color;
      }
    }
    &:hover {
      background-color: orange !important;
      color: $white-color !important;
    }
  }
  &.mat-selected {
    .mat-option-pseudo-checkbox {
      background: transparent;
      &:after {
        color: $black-color;
      }
    }
    &:focus {
      background: transparent !important;
    }
    &:hover {
      background-color: orange !important;
      color: $white-color !important;
      .mat-option-pseudo-checkbox:after {
        color: $white-color;
      }
    }
  }
}
.mat-option {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  line-height: 48px;
  height: 48px;
  padding: 0 16px;
  text-align: left;
  text-decoration: none;
  position: relative;
  cursor: pointer;
  outline: none;
  margin: 0 5px;
  border-radius: 2px;

  .mat-option-text {
    float: left;
  }
  span .mat-option-pseudo-checkbox {
    float: right;
  }
}
.mat-option {
  font-family: Roboto, "Helvetica Neue", sans-serif;
  font-size: 16px;
  .mat-option-ripple.mat-ripple {
    display: none;
  }
}
.mat-option.mat-selected:not(.mat-option-multiple) {
  box-shadow: 0 4px 20px 0px rgba(252, 251, 251, 0.14), 0 7px 10px -5px orange;
}
.mat-form-field-infix {
  width: auto !important;
}
.mat-select {
  .mat-select-value-text span {
    font-size: 14px;
  }
}

